#pricetag {
        background: linear-gradient(to right, #ecf5fd 50%, white 50%);
        background-size: 200% 100%;
        background-position: right bottom;
        color: #97d5ac;
        transition: all .5s ease-out;
}

#pricetag.border-left {
        border-left-color: #ecf5fd!important;
        border-left-width: 3px!important;
        border-bottom-left-radius: 3px!important;
        border-bottom-right-radius: 3px!important;
}

#pricetag:hover {
        background-position: left bottom;
        color: #28a745;
}

#pricetag p {
        font-weight: bold;
        font-size: 1.2rem;
}

#codewithlove {
        margin: 0px!important;
        opacity: 0.25!important;
}

#quotes.container-fluid,
#quotes.container-fluid [class^="col-"],
#quotes.container-fluid .row {
        padding: 0!important;
}

#quotes.container-fluid .row {
        margin: 0!important;
}

#quote {
  animation-name: bg_slide;
  animation-duration: 88s;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-fill-mode: none;
  animation-play-state: running;
}



@-webkit-keyframes bg_slide {
 from {
  background-position:0 center
 }
 to {
  background-position:-939px center
 }
}
@keyframes bg_slide {
 from {
  background-position:0 center
 }
 to {
  background-position:-939px center
 }
}



