/* Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {

        main .card {
                font-size: 12px;
        }

        main .card h5 {
                font-size: 16px;
                font-weight: bold;
        }

        main .card h6 {
                font-size: 14px;
        }

        #cardMainQuote .blockquote {
                font-size: 16px;
        }

        #quotes .blockquote {
                font-size: 1.025rem;
        }

        #quote {
                padding: 300px 0 300px 0;
                background: url(/assets/images/cloud-tile-soothing-600.png) repeat-x; /* ukuran size ini kenapa harus 940 karena background position di @keyframes */
                /* background-size: auto 120px; */
                overflow: hidden;
        }

    #nazhifaHeader {
            height: 1.865rem;
            margin-top: 4rem;
            margin-bottom: 4rem;
            transition: 0.3s;
    }

    #display-container .card { /*harusnya di id in karena card yang lain jadi kena */
            margin-right: 5px!important;
            margin-left: 5px!important;
    }

    #display-container .card h5 { 
        font-size: 16px!important;
        font-weight: bold!important;
    }

    #display-container .card h6 { 
        font-size: 14px!important;
    }

    #display-container .card-deck { /*harusnya di id in karena card yang lain jadi kena */
            margin-right: 5px!important;
            margin-left: 5px!important;
    }


}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
        main .card {
                font-size: 12px;
        }

        main .card h5 {
                font-size: 14px;
                font-weight: bold;
        }

        main .card h6 {
                font-size: 13px;
        }

        #cardMainQuote .blockquote {
                font-size: 14px;
        }

        #quotes .blockquote {
                font-size: 1.025rem;
        }

    #nazhifaHeader {
            height: 1.5rem;
            margin-top: 2rem;
            margin-bottom: 2rem;
            transition: 0.3s;
            /* background-color: lightblue; */
    }

    #display-container .card { /*harusnya di id in karena card yang lain jadi kena */
            margin-right: 5px!important;
            margin-left: 5px!important;
            margin-bottom: 15px!important;
    }

    #display-container .card h5 { 
        font-size: 16px!important;
        font-weight: bold!important;
    }

    #display-container .card h6 { 
        font-size: 14px!important;
    }

    #display-container .card-title { 
        max-width: 10.8rem;
        display: block;
        white-space: nowrap; 
        overflow: hidden;
        text-overflow: ellipsis;
    }

    #display-container .card-subtitle { 
        max-width: 10.8rem;
        display: block;
        white-space: nowrap; 
        overflow: hidden;
        text-overflow: ellipsis;
    }

    #display-container .card-deck { /*harusnya di id in karena card yang lain jadi kena */
            margin-right: 5px!important;
            margin-left: 5px!important;
    }

    #Popular .card-title { 
        max-width: 13.2rem;
